body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
table,
th,
td {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

%list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

%link {
  text-decoration: none;
  color: currentColor;
}

%button {
  padding: 0;
  font-family: inherit;
  cursor: pointer;
}
