:root {
  --white-main-color: #fff;
  --black-main-color: #111321;
  --body-color-light-theme: #f4f4f4;
  --body-color-dark-theme: #686868;
  --grey-main-color: #a8a8a8;
  --blue-main-color: #4440f6;
  --blue-color: #4440f7;
  --blue-secondary-color: #4b48db;
  --transparent-blue-color: rgba(68, 64, 247, .7);
  --transparent-secondary-blue-color: rgba(68, 64, 247, .4);
  --dark-color: #2e2e2e;
  --green-color: #00dd73;
  --blured-card-backdrop-color: rgba(244, 244, 244, .4);
  --transparent-backdrop-location-weather-card: rgba(254, 254, 255, .3);
  --dark-grey-text-color: #5f6775;
  --light-grey-text-color: #a8a8a8;
  --main-black-text-color: #111321;
  --white-text-color: #fff;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loading {
  width: 100px;
  height: 100px;
  border: 10px solid var(--light-grey-text-color);
  border-left-color: var(--blue-main-color);
  opacity: 0;
  border-radius: 50%;
  margin: auto;
  animation: spin 1s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loading.show {
  opacity: 1;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

