.slider {
  cursor: pointer;
  background-color: var(--white-text-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background: var(--blue-secondary-color);
  background-position: center;
  background-repeat: no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.switch__input:checked + .slider {
  background-color: var(--blue-color);
}

.switch__input:checked + .slider:before {
  background: var(--white-text-color);
  background-position: center;
  background-repeat: no-repeat;
  transform: translateX(22px);
}

.slider.round {
  border: 1px solid var(--blue-secondary-color);
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

